@import "reset";
@import "mixins";
@import "fonts";
@import "vars";
// common styles
:root {
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
}

html,
body {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-family: $font-primary;
  @include rfs(15);
}

.frontpage {
  height: 100%;
  background-color: #336699;
  background-image: url("../img/bg__night-city.jpg");
  background-size: cover;
}

.container {
  display: flex;
  flex-direction: column;
  max-width: 1160px;
  height: 100%;
  margin: 0 auto;
  @media only screen and (max-width: 1200px) {
    padding-left: 1%;
    padding-right: 1%;
  }
}

// header
.header {
  padding-top: 41px;
  display: flex;
  @media only screen and (max-width: 768px) {
    justify-content: center;
  }
  .header__logo {
    width: 150px;
    img {
      min-width: 100px;
      width: 100%;
    }
  }
}

// main content
.frontpage__content {
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  .input-block__title {
    margin-bottom: 37px;
    @include rfs(38px);
    text-align: center;
    color: #fff;
  }
  .input-block {
    width: 100%;
    padding-bottom: 12px;
  }
  .input-block__form {
    margin: 0 auto;
    width: 100%;
    max-width: 825px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @media only screen and (max-width: 825px) {
      width: 100%;
    }
  }
  .form__notify,
  input {
    height: 2.8em;
    @include rfs(20px);
    padding-left: 12px;
    border: 1px solid transparent;
  }
  .form__address {
    flex-grow: 1;
    flex-shrink: 10;
    margin-right: -3px;
    padding-right: 10px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  .form__unit {
    max-width: 230px;
  }
  .form__notify {
    margin-left: -2px;
    padding-left: 20px;
    padding-right: 20px;
    font-weight: 500;
    text-align: center;
    color: #fff;
    background-color: #8cc63f;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    box-sizing: border-box;
    &:hover {
      cursor: pointer;
      background-color: darken(#8cc63f, 5%);
    }
  }
  .form__notify:focus,
  input:focus {
    outline: none;
  }
  input::-webkit-input-placeholder {
    color: #2e2e2e;
  }
  input:-ms-input-placeholder {
    color: #2e2e2e!important;
  }
  input::placeholder {
    color: #2e2e2e;
  }
  @media only screen and (max-width:600px) {
    .form__notify,
    input {
      width: 100%;
      margin-bottom: 15px;
      border-radius: 3px
    }
    .form__notify {
      margin-top: 30px;
      max-width: 50%;
    }
    .form__unit {
      max-width: 100%;
    }
  }
}

// footer
.footer {
  flex: 0 0 auto;
}

.footer__menu {
  display: flex;
  padding-bottom: 46px;
  justify-content: space-between;
  @media only screen and (max-width: 480px) {
    padding-bottom: 23px;
  }
}

.footer__menu-item {
  margin-right: 25px;
  @include rfs(17px);
  color: #fff;
  white-space: nowrap;
  transition: all .2s ease;
  &:hover {
    color: #8cc63f;
  }
  &:last-child {
    margin-right: 0;
  }
}

// STAGE PAGES
.stages-wrap {
  width: 100%;
  // height: 100%;
  // padding-left: 1%;
  // padding-right: 1%;
  overflow-x: hidden;
  font-family: $font-primary;
  text-align: center;
}

// common styles
.timeline {
  margin: 43px auto 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  .timeline__circle {
    width: 48px;
    height: 48px;
    line-height: 47px;
    color: $c_grey;
    font-size: 20px;
    border: 1px solid #fff;
    border-radius: 50%;
  }
  .timeline__check {
    display: none;
  }
  .timeline__progress {
    width: 11.56vw;
    height: 2px;
    background-color: #fff;
  }
  @media screen and (max-width: 967px) {
    .timeline__progress {
      width: 20vw;
    }
  }
}

.content {
  max-width: 945px;
  margin: 0 auto;
  padding: 39px 67px 30px 68px;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 12px rgba(2, 3, 3, 0.1);
  overflow: hidden;
  border-radius: 3px;
  @media screen and (max-width: 480px) {
    padding-left: 1%;
    padding-right: 1%;
  }
  // title
  .header__title {
    margin-bottom: 30px;
    font-size: 30px;
    color: rgba(0, 0, 0, .9);
    ;
    text-transform: uppercase;
  }
  .header__subtitle {
    font-size: 24px;
    color: $c_blue;
    font-weight: 400;
  }
  // main content
  &__main {
    margin-top: 33px;
  }
  // all buttons
  button {
    height: 36px;
    line-height: 36px;
    color: #fff;
    font-family: $font-primary;
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
    background: $c_blue;
    border: none;
    box-sizing: border-box;
    border-radius: 3px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
    &:hover {
      cursor: pointer;
      background-color: darken($c_blue, 3%);
    }
  }
}

// common styles end
// CUSTOM STYLE TO EACH STAGE
// STAGE 0
.stage--0 {
  .timeline {
    display: none;
  }
  .content {
    margin-top: 122px;
    padding-bottom: 21px;
    .header__title {
      margin-top: 12px;
      margin-bottom: 2px;
      font-size: 40px;
      font-weight: bold;
    }
    .header__subtitle {
      font-size: 24px;
      color: $c_blue;
      font-weight: 400;
    }
    .content__main {
      margin-top: 60px;
    }
    .content__question-container {
      display: none;
    }
  }
  .main__find {
    margin: 0 auto;
    .main__address {
      height: 59px;
      line-height: 59px;
      padding-left: 20px;
      padding-right: 11.3vw;
      margin-right: 0;
      color: $yummy_black;
      font-size: 18px;
      font-weight: 400;
      border: 1px solid $c_grey;
    }
    .main__search {
      height: 59px;
      line-height: 59px;
      margin-left: -6px;
      padding: 0px 5.1vw;
    }
  }
  .content__footer {
    margin-top: 140px;
    text-align: left;
    .client__text {
      padding: 0 60px 15px 30px;
      text-align: left;
      color: $yummy_black;
      font-size: 18px;
      font-weight: 400;
      line-height: 26px;
      border-bottom: 3px solid rgba(63, 61, 61, 0.5);
    }
    .client__profile {
      display: flex;
      align-items: center;
    }
    .client__avatar {
      height: 70px;
      width: 70px;
      margin: 14px;
      overflow: hidden;
      border-radius: 50%;
    }
    .client__author {}
    .client__name {
      color: $c_blue;
      font-size: 20px;
      font-weight: 400;
      line-height: 26px;
    }
    .client__subtitle {
      margin-top: -4px;
      color: $yummy_black;
      font-family: Roboto;
      font-size: 16px;
      line-height: 26px;
    }
  }
  @media screen and (max-width: 945px) {
    .content {
      padding: 45px 30px 30px 30px;
    }
  }
  @media screen and (max-width: 768px) {
    .content__main {
      display: flex;
      justify-content: center;
      .main__address {
        padding-right: 25vw;
      }
      .main__search {
        margin-top: 30px;
        padding: 0px 10vw;
      }
    }
    .content__footer {
      margin-top: 100px;
      .client__text {
        padding: 0 0 20px 0;
      }
    }
  }
}

// STAGE 1
.stage--1 {
  .timeline {
    .timeline__circle--one {
      border: 2px solid $green;
    }
  }
  .content {
    padding-left: 0;
    padding-right: 0;
  }
  #map {
    width: 100%;
    height: 377px;
    position: relative;
  }
  .content__footer {
    margin-top: 26px;
    padding-left: 45px;
    padding-right: 30px;
  }
  .content__question-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    .content__question {
      max-width: 38%;
      color: $yummy_black;
      font-size: 18px;
      line-height: 22px;
      text-align: left;
    }
    .content__answer-btn {
      display: flex;
      flex-basis: 50%;
      justify-content: center;
    }
    .content__answer {
      padding-left: 62px;
      padding-right: 62px;
      &--no {
        background-color: $yellow;
        margin-right: 24px;
        &:hover {
          background-color: darken($yellow, 3%);
        }
      }
      &--yes {}
    }
    @media screen and (max-width: 945px) {
      .content__question {
        max-width: 100%;
      }
      .content__answer {
        display: inline-block;
        margin-top: 30px;
        padding: 0 12vw;
        &--no {
          margin-right: 24px;
        }
      }
    }
  }
  @media screen and (max-width: 945px) {
    .content__question-container {
      justify-content: space-around;
    }
  }
}

// common styles for stage2 and stage3
.content__details {
  .details__column {
    display: flex;
    text-align: left;
    label {
      display: block;
      margin-bottom: 9px;
      font-size: 16px;
      font-family: $font-primary;
      color: $yummy_black;
    }
    input,
    select {
      display: block;
      width: 99%;
      height: 40px;
      margin-bottom: 17px;
      padding-left: 10px;
      font-family: $font-primary;
      font-size: 14px;
      color: #757575;
      border: 1px solid #ccc;
      background-color: #fff;
      box-sizing: border-box;
      border-radius: 3px;
    }
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      background-image: url("../img/select__arrow.png");
      background-position: calc(100% - 9px);
      background-size: 17px 12px;
      background-repeat: no-repeat;
      &:hover {
        cursor: pointer;
      }
    }
    .details__column--one,
    .details__column--two {
      width: 49%;
    }
    .details__column--two {
      margin-left: 30px;
    }
  }
  .details__submit {
    margin: 27px auto 16px;
    padding: 0 36px;
    text-transform: none;
    span {
      color: #cbcbcb;
      font-size: 12px;
    }
  }
  @media screen and (max-width: 768px) {
    .details__column {
      flex-wrap: wrap;
      .details__column--one,
      .details__column--two {
        width: 100%;
      }
      .details__column--two {
        margin-left: 0;
      }
    }
    .details__submit {
      margin-top: 45px!important;
    }
  }
}

// end of common styles for stage2 and stage3
// STAGE 2
.stage--2 {
  .timeline {
    .timeline__progress--one {
      background-color: $green;
    }
    .timeline__circle--one {
      border: none;
      background-color: $green;
      .timeline__number--one {
        display: none;
      }
      .timeline__check {
        display: inline-block;
        color: #fff;
      }
    }
    .timeline__circle--two {
      border: 2px solid $green;
    }
  }
  .content__main {
    margin-top: 34px;
    font-size: 18px;
  }
}

// STAGE 3
.stage--3 {
  .timeline {
    .timeline__progress {
      background-color: $green;
    }
    .timeline__circle--one {
      border: none;
      background-color: $green;
      .timeline__number--one {
        display: none;
      }
      .timeline__check {
        display: inline-block;
        color: #fff;
      }
    }
    .timeline__circle--two {
      border: none;
      background-color: $green;
      .timeline__number--two {
        display: none;
      }
      .timeline__check {
        display: inline-block;
        color: #fff;
      }
    }
    .timeline__circle--three {
      border: 2px solid $green;
    }
  }
  .content__main {
    margin-top: 35px;
  }
  .content .header__title {
    margin-bottom: 0px;
  }
  .details__submit {
    height: 48px;
    padding-top: 5px;
    line-height: 15px;
    margin-top: 177px;
    margin-bottom: 25px;
  }
}